async function _basePostRequest(url, params) {
    try {

        const { data } = await axios.post(url, params)
        return data
    } catch (e) {
        console.log(e)
        return e
    }
}

const searchDonor = (params) => {
    const res = _basePostRequest(BASE_URL + "article/check", params)
    return res
}

const postArticle = (params) => {
    const res = _basePostRequest(BASE_URL + "article", params)
    return res;
}

const postSportStar = (params) => {
    const res = _basePostRequest(BASE_URL + 'sports', params)
    return res
}

const postCrimson = (params) => {
    const res = _basePostRequest(BASE_URL + "crimson", params)
    return res
}

const postDonor = (params) => {
    const res = _basePostRequest(BASE_URL + "donor", params)
    return res;
}

export default {
    searchDonor,
    postArticle,
    postSportStar,
    postCrimson,
    postDonor
}
